@import "../utils/_variables";
@import "../utils/_mixins";
@import "../../libs/include-media/dist/_include-media";

/* products */
.products {
	overflow: hidden;
	margin-bottom: $bottom-space--md;

	&:last-child {
		margin-bottom: 0; } }

.products__list {
	@include clear-spacing;
	padding-bottom: 1px; }

.products__item {
	@include display-inline-block-top;
	width: 100%;
	position: relative;
	padding-top: 1px;
	@include font(1.5rem, round-float(18px/15px, 2));
	background-color: #{$bg-color-selected};
	letter-spacing: normal; }

.products__inner {
	display: block;
	@include padding-vertical(13px);
	text-decoration: none;
	color: inherit;
	@include clear-spacing;
	transition-property: background-color;
	transition-duration: $animation-speed;

	.no-touchevents & {
		&:hover, &:focus {
			background: $bg-color-selected; } } }

// дополнительные бордеры нужны для safari
// т.к. в этом браузере не поддерживается автовысота во flex-box элементах
// подходит только, если не нужно делать всю область кликабельной
.products, .products__list, .products__item, .products__inner {
	position: relative;

	&::before, &::after {
		@include pseudo();
		@include size(0);
		border: 0 solid $border-color; } }

.products {
	&::after {
		left: 0;
		bottom: 0;
		border-width: 0 0 1px;
		@include size(9999px, 0); } }

.products__list, .products__item {
	&::before {
		right: -1px;
		top: 0;
		@include size(0, 9999px);
		border-width: 0 1px 0 0;
		display: none; } }

.products__list {
	&::after {
		top: 0;
		left: 0;
		@include size(9999px, 0);
		border-width: 0 0 1px 0; } }

.products__item {
	&::after {
		top: 0;
		left: 0;
		@include size(0, 9999px);
		border-width: 0 0 0 1px;
		display: none; } }

.products__inner {
	&::after {
		top: -1px;
		left: 0;
		@include size(9999px, 0);
		border-width: 1px 0 0; } }

// дополнительные бордеры нужны для safari END

.products__body, .products__footer {
	position: relative;
	@include display-inline-block-middle; }

.products__body {
	width: 60%;

	@include media('<desk-lg') {
		width: 55%; } }

.products__footer {
	width: 39.98%;
	padding-left: 1.5%;

	@include media('<desk-lg') {
		width: 44.98%; } }

.products__figure, .products__content, .products__title, .products__stock {
	@include display-inline-block-middle; }

$product-figure-width: 60px;
$product-figure-right-space: 20px;
.products__figure {
	position: relative;
	width: #{$product-figure-width};
	margin-right: #{$product-figure-right-space};
	letter-spacing: normal;

	&:before {
		@include pseudo-block-relative();
		@include ratio(4, 3); } }

.products__img {
	@include cover-abs();
	font-size: $font-size;
	letter-spacing: normal;
	backface-visibility: hidden;

	img {
		display: block;
		max-height: 100%;
		max-width: 100%;
		@include centering-transform-rel(); } }

.products__content {
	width: calc(100% - (#{$product-figure-width} + #{$product-figure-right-space}));

	ul {

		li, ul, ol {
			margin: 0.7em 0; }

		li {
			padding-left: 0;

			&::before {
				content: none; } }

		a {
			padding-top: 0.2em;
			color: inherit;
			text-decoration: none;
			transition-property: color;
			transition-duration: $animation-speed;

			.no-touchevents &:hover {
				color: $link-color; } }

		ul, ol {
			padding-left: 30px;

			li {
				&::before {
					content: '–';
					display: inline-block;
					position: relative;
					margin-right: 5px;
					@include position(auto, auto);
					width: auto;
					height: auto;
					border: none; } } } } }

.products__title {
	width: 70%;
	@include font-alt-bold;
	padding-right: 20px;
	@include font(16px, 19px);
	letter-spacing: normal;
	transition-property: color;
	transition-duration: $animation-speed;

	a {
		text-decoration: none;
		color: inherit;
		transition: inherit;
		padding-top: 0.2em;

		.no-touchevents &:hover {
			color: $link-color; } } }

.products__stock {
	position: relative;
	@include font(14px, 18px);
	letter-spacing: normal;
	width: 29%;

	& > *:last-child {
		margin-bottom: 0; } }

.products__price, .products__options {
	@include display-inline-block-middle;
	letter-spacing: normal; }

.products__options {
	width: 170px; }

.products__price {
	width: calc(100% - 170px);
	text-align: right;
	padding-right: 15%;

	.cur, .no, .old {
		margin-bottom: 2px; }

	.cur {
		@include font(18px, 22px);
		@include font-bold; }

	.no {
		@include font(14px, 18px); }

	.old {
		@include font(14px, 22px);
		@include font-normal;
		color: #666;
		text-decoration: line-through; } }

.products__options {
	text-align: center;

	.btn-outline {
		display: block;
		@include padding-horizontal(12px);

		.in-cart & {
			display: none; } }

	.spinner {
		display: none;

		.in-cart & {
			display: inline-block; } } }

/* grid view */
.products.grid-view-activated {
	& {
		padding-right: 1px; }

	.products__list {
		display: flex;
		flex: 0 1 auto;
		//justify-content: stretch
		flex-flow: row wrap; }

	.products__item {
		@include display-inline-block-top;
		padding-left: 1px;
		width: round-float(100%/5, 4);

		@include media('<desk-hd') {
			width: round-float(100%/4, 4); }

		@include media('<desk-lg') {
			width: round-float(100%/3, 4); }

		@include media('<desk-sm') {
			width: round-float(100%/2, 4); }

		@include media('<mob-lg') {
			width: round-float(100%/1, 4); } }

	.products__inner {
		padding: 30px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 100%; }

	.products__list, .products__item {
		&::before {
			display: block; } }

	.products__item {
		&::after {
			display: block; } }

	.products__body, .products__footer {
		display: block;
		width: auto;
		@include padding-horizontal(0); }

	.products__figure, .products__content, .products__title, .products__stock, .products__price, .products__options {
		display: block;
		width: auto; }

	.products__figure {
		margin: 0 auto $bottom-space;
		width: 100%;
		max-width: 260px; }

	.products__title {
		margin-bottom: 0.5em;
		padding: 0; }

	.products__stock {
		margin-bottom: 1em; }

	.products__price {
		text-align: center;
		padding: 0;
		margin-bottom: 20px;

		.cur, .no, .old {
			@include display-inline-block-top;
			@include margin-horizontal(0.2em);
			margin-bottom: 0; } } }

// для маленьких мониторов
@include media('<tab-lg') {
	.products {
		padding-right: 1px; }

	.products__list {
		display: flex;
		flex: 0 1 auto;
		flex-flow: row wrap; }

	.products__item {
		@include display-inline-block-top;
		padding-left: 1px;
		width: round-float(100%/2, 4); }

	.products__inner {
		padding: 30px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 100%; }

	.products__list, .products__item {
		&::before {
			display: block; } }

	.products__item {
		&::after {
			display: block; } }

	.products__body, .products__footer {
		display: block;
		width: auto;
		@include padding-horizontal(0); }

	.products__figure, .products__content, .products__title, .products__stock, .products__price, .products__options {
		display: block;
		width: auto; }

	.products__figure {
		margin: 0 auto $bottom-space;
		width: 100%;
		max-width: 260px; }

	.products__title {
		margin-bottom: 0.5em;
		padding: 0;

		// только для маленьких мониторов
		text-align: center; }

	.products__stock {
		margin-bottom: 1em;

		// только для маленьких мониторов
		text-align: center; }

	.products__price {
		text-align: center;
		padding: 0;
		margin-bottom: 20px;

		.cur, .no, .old {
			display: block;
			@include margin-horizontal(0.2em);
			margin-bottom: 0; } } }

.products__item {
	@include media('<mob-lg') {
		width: round-float(100%/1, 4); } }

/* grid view end */
/* products end */
