@import "../utils/_variables";
@import "../utils/_mixins";
@import "../../libs/include-media/dist/_include-media";

/* options panel */
.p-options {
	position: relative;
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.p-options__list {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;

	.no-flexbox & {
		display: table;
		width: 100%;
		table-layout: fixed; }

	@include media('<tab-sm') {
		&, .no-flexbox & {
			display: block; } } }

.p-options__item {
	@include font(1.4rem, 1.3);
	text-align: center;
	flex: 3 3 100px;

	@include media('<tab-lg') {
		flex: 1 1 100px;

		&:nth-last-child(2) {
			text-align: right;
			flex: 1 1 100px; } }

	&:first-child {
		text-align: left;
		flex: 1 1 100px; }

	&:last-child {
		text-align: right;
		flex: 1 1 100px; }

	.no-flexbox & {
		display: table-cell;
		vertical-align: middle; }

	@include media('<tab-sm') {
		margin-bottom: $bottom-space--sm;
		text-align: center;

		&:first-child, &:last-child {
			text-align: center; }

		.no-flexbox & {
			display: block; } }

	.view-switcher {
		@include display-inline-block-top; } }

//.view-switcher-wrap
//include media('<tab-lg')
//display: none

.p-options__inner {
	position: relative; }

.p-options__group {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	user-select: none;
	text-align: left;

	@include media('<tab-lg') {
		justify-content: flex-end; }

	@include media('<tab-sm') {
		justify-content: center; }

	& > small {
		@include font(inherit, inherit);
		@include display-inline-block-middle;
		margin: 4px 10px 4px 0; }

	& > select {
		display: none; } }

.p-sorting {
	@include display-inline-block-middle;

	&, .#{$user-content} & {
		margin: 0;

		li {
			padding: 2px 8px 2px 8px;
			margin: 4px 4px 4px 0;

			&::before {
				content: none; }

			&.active {
				padding-right: 24px; } } }

	li {
		@include display-inline-block-top;
		white-space: nowrap;
		position: relative;
		cursor: pointer;

		& > span {
			border-bottom: 1px solid #{$border-color};
			transition-property: border-color;
			transition-duration: $animation-speed; }

		// стрелочка развернута ВВЕРХ, сортировака ПО ВОЗРАСТАНИЮ
		.arr {
			position: absolute;
			top: 50%;
			right: 10px;
			@include triangle(top, #{$text-color}, 4px, 2);
			margin-top: -4px;
			opacity: 0;
			visibility: hidden;
			transition-property: opacity, visibility;

			.order-desc & {
				transform: rotate(0.5turn); } }

		// состояние на ховер
		.no-touchevents &:hover {

			& > span {
				border-bottom-color: transparent; } }

		// активное соотояние
		&.active {
			background-color: #{$border-color};

			& > span {
				border-bottom-color: transparent; }

			// стрелочка развернута ВНИЗ, сортировака ПО УБЫВАНИЮ
			.arr {
				opacity: 1;
				visibility: visible;
				transition-duration: $animation-speed; } } } }

/* options panel end */
