@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* view switcher */
$view-switcher-size: 38px, 18px; // width, height
$view-switcher-icon-size: 16px;

.view-switcher {
	position: relative;
	margin-bottom: $bottom-space--md;

	&::before {
		@include pseudo();
		top: 50%;
		right: 100%;
		@include size(100px, 0);
		border-top: 1px solid $tape-slider-color;
		margin-top: -1px;
		margin-right: 50px; }

	&:last-child {
		margin-bottom: 0; }

	a {
		float: left;
		position: relative;
		margin-left: 20px;
		@include size(nth($view-switcher-size, 1), nth($view-switcher-size, 2));
		color: $text-color;
		fill: $text-color;
		z-index: 9;

		&:first-child {
			margin-left: 0; }

		span {
			@include hide-text; }

		svg {
			@include size(nth($view-switcher-size, 1), nth($view-switcher-size, 2)); }

		path {
			fill: inherit; }

		&:hover {
			color: $base-color;
			fill: $base-color; }

		&.active {
			color: $base-color;
			fill: $base-color;
			cursor: default; } } }

/* view switcher end */
